$dark-100: #1f2041;
$dark-75: rgba(31, 32, 65, 0.75);
$dark-50: rgba(31, 32, 65, 0.5);
$dark-25: rgba(31, 32, 65, 0.25);
$dark-5: rgba(31, 32, 65, 0.05);
$purple: #bc9cff;
$green: #6fcf97;

@mixin font-body($weight: normal, $size, $height) {
  font-family: Montserrat;
  font-style: normal;
  font-weight: $weight;
  font-size: $size;
  line-height: $height;
}
